import { keyframes } from 'styled-components'

const slideUp = keyframes`
  from {
    transform: translateY(100%)
  }
  to {
    transform: translateY(0px)
  }
`

const slideDown = keyframes`
  from {
    transform: translateY(0px)
  }
  to {
    transform: translateY(100%)
  }
`

const transitions = {
  slideUp,
  slideDown,
}

export default transitions
